.signup {
    display: flex;
    align-items: center;
    justify-content: center;
    /* height: 120vh; */
    background-image: linear-gradient(#629eff ,#ec70ff, #629eff);
    background-position: center;
    padding: 40px;
}
form {
    background-color: #fff;
    padding:0px 100px;
    border-radius: 10px;
}
.head {
    color: rgb(97, 0, 135);
    font-size: 1.7rem;
    font-weight: bolder;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 20px;
}
.butn {
    width: 80%;
    height: 50px;
    padding: 10px;
    border: none;
    background-color: rgba(114, 0, 159, 0.676);
    color: #fff;
    border-radius: 10px;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 10px;
}

input {
    padding: 12px;
    margin: 10px 0px;
    border-radius: 5px;
    border: 1px solid rgb(97, 0, 135);
    width: 80%;
}

label {
    font-size: 15px;
    color: rgb(97, 0, 135);;
   
}
span {
    font-size: 12px;
    color: red;
    padding: 3px;
    display: none;
}
input:invalid[focused="true"] {
    border: 1px solid red;
}

input:invalid[focused="true"] ~ span {
    display: block;
}
.link {
    padding: 20px;
}