@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Source+Sans+Pro:wght@200&display=swap');
.box {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background:linear-gradient(#7340f3, rgb(97, 0, 135));
    color: azure;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 16px;
}
.logo {
    font-family: Lobster;
    font-size: 20px;
}

.list {
    list-style-type: none;
    font-family: Lobster;
}
.list .item {
    display: inline-block;
    padding: 0px 20px;
}
.modal-close i{
  font-size: 1.2rem;
  font-weight: bold;
}
.modal-close{
    position: absolute;
    top: 0;
    right: 0;
}
