.signup {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-image: linear-gradient(#629eff ,#ec70ff, #629eff);
    background-position: center;
}
form {
    background-color: #fff;
    padding:0px 100px;
    border-radius: 10px;
}
h1 {
    color: rgb(97, 0, 135);;
    text-align: center;
}
.butn {
    width: 100%;
    height: 50px;
    padding: 10px;
    border: none;
    background-color: rgba(114, 0, 159, 0.676);
    color: #fff;
    border-radius: 10px;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 10px;
}
.link {
    padding: 20px;
}