.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modal-main {
    position:fixed;
    background: white;
    color: black;
    /* width: 60%; */
    height: auto;
    top:50%;
    left:50%;
    padding: 40px;
    transform: translate(-50%,-50%);
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }
  .search {
    width: 100%;
  }
  form {
    display: flex;
    justify-content:space-between;
    align-items:center;
    flex-direction: column;
    gap: 10px;
  }
  .add{
    font-family: 'Titan One', cursive;
  font-weight: bolder;
  color: rgba(132, 0, 255, 0.63);
  font-size: 1.5rem;
  }
  .foot {
    display: flex;
    flex-direction: row;
    gap: 20px;
  }
  .create {
    background:#629eff;
    color: #fff;
    border: 2px solid #629eff;
    width: 200px;
    height: 30px;
    border-radius: 5px;
    font-size: 1rem;
  }
  .create:hover{
  background-color: #0464ffcb;
  
  }
  .exit {
    background: rgba(132, 0, 255, 0.429);
    color: #fff;
    border: 2px solid rgba(132, 0, 255, 0.429);
    border-radius: 3px;
    font-size: 0.9rem;
  }
  .exit:hover{
    background-color: rgb(76, 2, 145);
  }
  .close i{
    font-size: 1.2rem;
    font-weight: bold;
  }
  .close{
      position: absolute;
      top: 0;
      right: 0;
  }