input {
    padding: 12px;
    margin: 10px 0px;
    border-radius: 5px;
    border: 1px solid rgb(97, 0, 135);
}
.input {
    display: flex;
    flex-direction:column;
    align-items: center;
    width: 280px;
}

label {
    font-size: 15px;
    color: rgb(97, 0, 135);;
   
}
span {
    font-size: 12px;
    color: red;
    padding: 3px;
    display: none;
}
input:invalid[focused="true"] {
    border: 1px solid red;
}

input:invalid[focused="true"] ~ span {
    display: block;
}
.signupform {
    padding: 20px;
}