@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Source+Sans+Pro:wght@200&display=swap');

.enclosure {
  width: 100vw;
  height: 90vh;
  /* background-color: bisque; */
  display: flex;
  justify-content: center;
  align-items: stretch;
  /* justify-items: center; */
  flex-wrap: nowrap;
}

.leftbar {
  font-family: 'Titan One', cursive;
  font-size: 1.03rem;
  background: linear-gradient(#629eff, #ec70ff, #629eff);
  color: #fff;
  width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: start;
  overflow-y: hidden;
}

.leftbar .chatperson {
  padding: 20px 5px;
  border-radius: 5px;
  margin: 6px;
}

.leftbar .chatperson:hover {
  background-color: rgba(118, 120, 121, 0.477);
  color: black;
  opacity: 0.8;
}

.leftbar .chatperson:active {
  background-color: rgba(132, 0, 255, 0.429);
}

.leftbar .addgrp {
  /* margin-left: 60px;
  margin-bottom: 50px; */
  position: fixed;
}

.centerbar {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  /* background-color: rgb(12, 205, 73); */
  color: rgba(54, 54, 54, 0.667);
  width: 60%;
  background-image: url('../static/image/chat-background.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  /* overflow-y: hidden; */
  gap: 5px;
}

.chat-box::-webkit-scrollbar {
  width: 15px;
}

.chat-box::-webkit-scrollbar-track {
  background-color: #e4e4e4;
  border-radius: 50px;
}

.chat-box::-webkit-scrollbar-thumb {
  background-image: linear-gradient(180deg, #D0368A 0%, #708AD4 99%);
  box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
  border-radius: 100px;
}

.centerbar .heading {
  font-family: 'Titan One', cursive;
  background: rgba(40, 40, 40, 0.663);
  font-weight: bolder;
  color: #629effcb;
  font-size: 1.5rem;
  padding: 10px 5px;
}

.chat-box {
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding: 0px 20px;
  overflow-y: scroll;
  flex-grow: 1;
}

.centerbar .to-users {
  font-family: 'Titan One', cursive;
  width: fit-content;
  max-width: 70%;
  /* background:linear-gradient(#ec70ff, #629eff) ; */
  background-color: #629eff;
  color: black;
  border-radius: 6px;
  margin-right: auto;
  padding: 7px;
  /* opacity: 0.9; */
}
.centerbar p{
  overflow-wrap: break-word;
}
.centerbar a{
  overflow-wrap: break-word;
}

.centerbar .from-users {
  font-family: 'Titan One', cursive;
  width: fit-content;
  max-width: 70%;
  /* background:linear-gradient( aquamarine, #629eff); */
  background-color: #67ffde;
  color: black;
  border-radius: 6px;
  margin-left: auto;
  padding: 7px;
  /* opacity: 0.9; */
}

.text-box {
  /* width: 100%; */
  height: auto;
  display: flex;
  /* justify-content: stretch; */
  flex-direction: row;
  margin-bottom: 10px;
  margin-right: 10px;
  margin-left: 7px;
  padding: 0;
  background-color: transparent;
  flex-grow: 0;
  justify-content: center;
}

.text-box input {
  height: auto;
  /* flex-grow: 1; */
  /* border-bottom-left-radius: 10px;
  border-top-left-radius: 10px; */
  font-size: 1rem;
  border: 2px solid rgb(97, 97, 97);
  margin: 0;
  padding: 7px 7px;
  background-color: rgb(97, 97, 97);
  outline: none;
}
.text-box input:focus{
  outline: none;
  /* background-color: rgba(151, 148, 148, 0.571); */
  background-color: rgb(97, 97, 97);
  color: #fff;
}

.text-box button {
  width: fit-content;
  height: fit-content;
  /* border-bottom-right-radius: 10px;
  border-top-right-radius: 10px; */
  margin: 0;
  padding: 7px 7px;
  background-color: rgb(97, 97, 97);
  color: #fff;
  outline: none;
  border: none;
  border-radius: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.text-box button .icon {
  font-size: 1.4rem;
  padding-top: 0.25rem;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

.rightbar {
  background: linear-gradient(#629eff, #ec70ff, #629eff);
  width: 20%;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.rightbar .groupname {
  font-family: 'Titan One', cursive;
  color: #fff;
  font-size: 1.3rem;
  font-weight: bolder;
}
ul {
  padding: 0;
}

.rightbar .groupusers {
  text-align: center;
  list-style-type: none;
  font-family: 'Titan One', cursive;
  color: #fff;
}
.add {
  background:#629eff;
  color: #fff;
  border: 2px solid #629eff;
  width: 90px;
  height: 30px;
  border-radius: 5px;
  font-size: 1rem;
}

.add-user {
  padding: 40px;
  margin: 30px;
}
.leave{
  font-size: 1.2rem;
  border-radius: 3px;
  border: 2px solid #fff;
  background-color: #fff;
 color: red;
}
.leave:hover{
 background-color: red;
 color: white;
 border: 2px solid red;
}

.delete {
  font-size: 1.2rem;
  border-radius: 3px;
  border: 2px solid red;
  background-color: red;
  color: #fff;
}

.delete:hover{
  background-color: #fff;
  color: red;
  border: 2px solid #fff;
 }