.center{
  /* padding: 90px 0; */
  height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    background: linear-gradient(#629eff ,#ec70ff, #629eff);
}
.center h3{
  color:rgb(97, 0, 135);  
  text-align: center;
  padding: 10px 0;
  border-bottom: 1px solid silver;
}
.resetForm{
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
  padding: 30px;
  box-sizing: border-box;
}
.resetForm input{
  /* position: relative; */
  border: none;
  background: none;
  outline: none;
  text-align: center;
  font-size: 1rem;
  border-bottom: 2px solid rgb(97, 0, 135);
  /* margin: 60px 0; */
}
.resetButn{
    padding: 10px;
    border: 2px solid rgba(114, 0, 159, 0.676);
    background-color: #fff;
    color: rgba(114, 0, 159, 0.676);
    border-radius: 5px;
    font-weight: bold;
    font-size: 0.9rem;
    cursor: pointer;
}
.resetButn:hover{
   border: 2px solid #fff;
    background-color:  rgba(114, 0, 159, 0.676);
    color: #fff;
}

